  border: 2px solid #CDCDCD;
}
.our-friend .our-friend__wrapper .our-friend__slider .our-friend__early--inactive:hover {
  cursor: auto;
  background-color: transparent;
  border: 2px solid #CDCDCD;
}
.our-friend .our-friend__wrapper .our-friend__slider .our-friend__next {
  width: 52px;
  height: 52px;
  background: url(../../assets/icons/arrow-right.svg) #fff;
  border: 2px solid #f1cdb3;
  border-radius: 100px;
  margin-top: 192px;
  background-repeat: no-repeat;
  background-position-x: 17px;
  background-position-y: 21px;
  background-color: transparent;
  transition: 0.3s;
}
.our-friend .our-friend__wrapper .our-friend__slider .our-friend__next:hover {
  transition: 0.3s;
  cursor: pointer;
  background-color: #FDDCC4;
  border: 2px solid #FDDCC4;
}
.our-friend .our-friend__wrapper .our-friend__slider .our-friend__next--inactive {
  background-image: url(../../assets/icons/arrow-right__inactive.svg);
  border: 2px solid #CDCDCD;
}
.our-friend .our-friend__wrapper .our-friend__slider .our-friend__next--inactive:hover {
  cursor: auto;
  background-color: transparent;
  border: 2px solid #CDCDCD;
}
.our-friend .our-friend__wrapper .our-friend__slider .our-friend__card {
  background: black;
  padding-bottom: 31px;
  border-radius: 10px;
  background: #FAFAFA;
  transition: 0.3s;
  animation: 0.5s ease-in-out;
  position: relative;
  text-align: center;
  animation-name: right;
  animation-duration: 0.5s;
}
.our-friend .our-friend__wrapper .our-friend__slider .our-friend__card:hover {
  transition: 0.3s;
  background-color: white;
  box-shadow: 0 1px 20px rgba(0, 0, 0, 0.05), 0 10px 10px rgba(0, 0, 0, 0.02);
}
.our-friend .our-friend__wrapper .our-friend__slider .our-friend__card .our-friend__name {
  font-family: "Georgia";
  color: #545454;
  font-size: 20px;
  text-align: center;
  padding-top: 26px;
  letter-spacing: 1px;
  padding-bottom: 31px;
}
.our-friend .our-friend__wrapper .our-friend__slider .our-friend__card .fullScreen {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}
.our-friend .our-friend__wrapper .our-friend__slider .our-friend__card .our-friend__button {
  position: relative;
  width: 188px;
  border-radius: 100px;
  background-color: transparent;
  padding: 13px;
  font-size: 17px;
  line-height: 130%;
  font-family: "Georgia";
  color: #292929;
  cursor: pointer;
  letter-spacing: 1px;
  border: 2px solid #f1cdb3;
  transition: 0.3s;
}
.our-friend .our-friend__wrapper .our-friend__slider .our-friend__card .our-friend__button:hover {
  transition: 0.3s;
  background-color: #fddcc4;
  border: 2px solid #fddcc4;
}
@keyframes right {
  from {
    left: 100%;
  }
  to {
    left: 0;
  }
}
@keyframes lefter {
  from {
    margin-right: 100%;
  }
  to {
    margin-left: 0;
  }
}
.our-friend .our-friend__wrapper .our-friend__slider .card__active,
.our-friend .our-friend__wrapper .our-friend__slider .card__next {
  display: block;
}
.our-friend .our-friend__wrapper .our-friend__slider .to-left {
  animation-name: left;
}
.our-friend .our-friend__wrapper .our-friend__slider .to-right {
  animation-name: right;
  animation-direction: reverse;
}
.our-friend .our-friend__wrapper .our-friend__slider .from-left {
  animation-name: left;
  animation-direction: reverse;
}
.our-friend .our-friend__wrapper .our-friend__slider .from-right {
  animation-name: right;
}
@keyframes left {
  from {
    left: 0%;
    opacity: 1;
  }
  to {
    left: -100%;
    opacity: 0;
  }
}
@keyframes right {
  from {
    left: 100%;
    opacity: 0;
  }
  to {
    left: 0%;
    opacity: 1;
  }
}
.our-friend .our-friend__wrapper .our-friend__slider .card__next {
